<template>
  <div class="notes">
    <div class="container">
      <div class="notes__top">
        <h2 class="notes__title">{{notes.length > 0 ? words.notesTitle[lang] : words.noNotesTitle[lang]}}</h2>
        <button class="notes__btn" @click="grid = !grid">
          <img src="@/assets/img/list.svg" alt="" v-if="grid"/>
          <img src="@/assets/img/grid.svg" alt="" v-else/>
          <span>{{grid ? words.notesListBtn[lang] : words.notesGridBtn[lang]}}</span>
        </button>
      </div>
      <div class="notes__list" :class="{active: !grid}">
        <NoteItem 
        :lang="lang"
        :grid="grid" 
        v-for="note in notes" 
        :key="note.id" 
        :note="note"
        @delNote="$emit('delNote', note.id)"
        @changeNote="$emit('changeNote', note.id)"
        />
      </div>
    </div>
  </div>
</template>

<script>

import NoteItem from '@/components/NoteItem.vue'

export default {
    components: {
        NoteItem
    },
    inject: ['words'],
    props: {
      lang: String,
      notes: {
        typeof: Array,
        required: true
      }
    },
    data(){
        return {
            grid: true
        }
    }
};
</script>

<style>
.notes {
  padding-top: 30px;
}
.notes__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}
.notes__title {
  font-weight: 400;
  font-size: 22px;
  line-height: 28px;
  color: #323232;
}
.notes__btn {
  width: 120px;
  height: 56px;
  background: linear-gradient(
      0deg,
      rgba(103, 80, 164, 0.11),
      rgba(103, 80, 164, 0.11)
    ),
    #fffbfe;
  box-shadow: 0px 4px 8px 3px rgba(0, 0, 0, 0.15),
    0px 1px 3px rgba(0, 0, 0, 0.3);
  border-radius: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 15px;
  cursor: pointer;
}
.notes__btn span {
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.1px;
  color: #6750a4;
}

.notes__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 24px;
    row-gap: 24px;
}
.notes__list.active {
  grid-template-columns: 1fr;
}
</style>