<template>
  <Navbar @searchValue="search = $event" :lang="lang" @changeLang="changeLang"/>
  <Notes :lang="lang" :notes="filterNotes" @delNote="delNote" @changeNote="changeNote"/>
  <Modal 
  :lang="lang"
  v-show="modalOpen" 
  @closeModal="closeModal" 
  :currentId="currentId" 
  @addNote="addNote"
  :edit="edit"
  :editNote="editNote"
  @editedNote="editedNote"
  />
  <AddButton @openModal="openModal"/>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Notes from "@/components/Notes.vue";
import Modal from "@/components/Modal.vue";
import AddButton from "@/components/AddButton.vue";
import langs from "@/lang";

export default {
  components: {
    Navbar,
    Notes,
    Modal,
    AddButton
  },
  data() {
    return {
      lang: 'ru',
      langwords: {},
      search: '',
      currentId: 1,
      edit: false,
      editNote: {},
      notes: [
        // {id: 1, title: "Vue", desc: "Lorem ipsum dolor sit amet.", date: '16.04.2023'},
        // {id: 2, title: "React", desc: "Lorem ipsum dolor sit amet.", date: '16.04.2023'},
        // {id: 3, title: "Angular", desc: "Lorem ipsum dolor sit amet.", date: '16.04.2023'},
        ],
        modalOpen: false,
    };
  },
  created(){
    this.getNotes();
    localStorage.lang = localStorage.lang || 'ru';
    this.lang = localStorage.lang || 'ru';
    this.langwords = langs;
    localStorage.words = JSON.stringify(this.langwords);
  },
  provide(){
    return {words: JSON.parse(localStorage.words)};
  },
  watch: {
    notes: {
      handler(newNotes){
        localStorage.notes = JSON.stringify(this.notes);
      },
      deep: true,
      
    }
  },
  computed: {
    filterNotes(){
      return this.search ? this.notes.filter(note => note.title.toLowerCase().includes(this.search.toLowerCase())) : this.notes;
    }
  },
  methods: {
    openModal(){
      this.modalOpen = true
    },
    closeModal(status){
      this.modalOpen = status;
      setTimeout(() => {
        this.edit = false;
      }, 500);
    },
    addNote(item){
      this.notes.push(item);
      this.modalOpen = false
    },
    delNote(id){
      let index = this.notes.findIndex( note => note.id == id);
      this.notes.splice(index, 1);
    },
    getNotes(){
      let localNotes = localStorage.notes;
      if(localNotes){
        this.notes = JSON.parse(localNotes);
        this.currentId =  this.notes.length;
        this.currentId++;
      }
    },
    changeNote(id){
      this.edit = this.modalOpen = true;
      let pickedNote = this.notes.find(note =>note.id == id);
      this.editNote = pickedNote;
    },
    editedNote(noteEdited){
      this.notes.forEach(note => {
        if(note.id == noteEdited.id){
          note.title = noteEdited.title;
          note.desc = noteEdited.desc;
          note.date = noteEdited.date;
        }
      });
    },
    changeLang(val){
      this.lang = localStorage.lang = val;
    }
  }
};
</script>

