<template>
  <Transition name="modal">
    <div class="modal" @click="closeModal">
      <form class="modal__block" @click.stop="">
        <h2 class="modal__title">{{edit ? words.changeNote[lang] : words.addNote[lang]}}</h2>
        <div class="modal__inputs">
          <label>
            <span>Title</span>
            <input type="text" placeholder="Title" v-model="title" required/>
          </label>
          <label>
            <span>Content</span>
            <textarea placeholder="Content" v-model="desc" required></textarea>
          </label>
        </div>
        <div class="modal__btns">
          <button class="modal__btn cancel" @click="closeModal">{{words.cancel[lang]}}</button>
          <button class="modal__btn add" @click="addNote" v-if="!edit">{{words.addBtn[lang]}}</button>
          <button class="modal__btn add" @click="cangeNote" v-else >{{words.changeBtn[lang]}}</button>
        </div>
      </form>
    </div>
  </Transition>
</template>

<script>
export default {
  inject: ['words'],
  props: {
    lang: String,
    currentId: Number,
    edit: Boolean,
    editNote: Object
  },
  data() {
    return {
      title: "",
      desc: "",
      id: this.currentId,
    };
  },
  methods: {
    closeModal() {
      this.$emit("closeModal", false);
      this.title = "";
      this.desc = "";
    },
    addNote() {
      if(this.title != '' && this.desc != ''){
        let item = {
          id: this.id++,
          title: this.title,
          desc: this.desc,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("addNote", item);
        this.title = "";
        this.desc = "";
      }
    },
    cangeNote(){
      if(this.title != '' && this.desc != ''){
        const editedNote = {
          id: this.editNote.id,
          title: this.title,
          desc: this.desc,
          date: new Date().toLocaleDateString(),
        }
        this.$emit('editedNote', editedNote);
        this.closeModal();
      }
    }
  },
};
</script>

<style>
.modal {
  background: rgba(0, 0, 0, 0.35);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal__block {
  background: linear-gradient(
      0deg,
      rgba(103, 80, 164, 0.11),
      rgba(103, 80, 164, 0.11)
    ),
    #fffbfe;
  border-radius: 28px;
  max-width: 312px;
  width: 100%;
  padding: 24px;
}
.modal__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
  color: #1c1b1f;
  margin-bottom: 16px;
}
.modal__inputs {
  display: flex;
  flex-direction: column;
  row-gap: 16px;
  margin-bottom: 24px;
}
.modal__inputs label {
  position: relative;
}
.modal__inputs span {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.4px;
  color: #6750a4;
  position: absolute;
  top: 8px;
  left: 16px;
}
.modal__inputs input,
.modal__inputs textarea {
  background: #e7e0ec;
  border-radius: 4px 4px 0px 0px;
  padding: 23px 16px 9px;
  width: 100%;
  border-bottom: 1px solid #1c1b1f;
}
.modal__inputs input::placeholder,
.modal__inputs textarea::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.5px;
  color: #49454f;
}
.modal__btns {
  display: flex;
  justify-content: flex-end;
  column-gap: 8px;
}
.modal__btn {
  border-radius: 5px;
  padding: 10px 12px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  letter-spacing: 0.1px;
  text-transform: uppercase;
  transition: 500ms;
}
.cancel {
  color: #cf1b1b;
}
.cancel:hover {
  background: #ffe1e1;
}
.add {
  color: #6750a4;
}
.add:hover {
  background: #e6ddff;
}
.modal-enter-active,
.modal-leave-active {
  transition: 200ms linear;
}
.modal-enter-from,
.modal-leave-to {
  opacity: 0;
  transform: scale(1.5);
}
</style>